import Vue from 'vue'
import Router from 'vue-router'
// import layout from '@/layout'
// import home from '@/pages/Home.vue'
import login from '@/layout/login'

import ywbl from '@/pages/ywbl'
import table from '@/pages/ywbl/gys/table.vue' // 3
import tabltableItem from '@/pages/ywbl/gys/tableItem.vue' // 3
import tableItemChild from '@/pages/ywbl/gys/tableItemChild.vue' // 3
import input from '@/pages/ywbl/gys/input.vue' // 3
import inputItem from '@/pages/ywbl/gys/inputItem.vue' // 3
import inputItemChild from '@/pages/ywbl/gys/inputItemChild.vue' // 3
import inputItemChild1 from '@/pages/ywbl/gys/inputItemChild1.vue' // 3
import infor from '@/pages/ywbl/gys/infor.vue' // 3

import glyMagessitem1 from '@/pages/ywbl/glyMagess/item1.vue' // 
import glyMagessitem2 from '@/pages/ywbl/glyMagess/item2.vue' // 
import glyMagessitem3 from '@/pages/ywbl/glyMagess/item3.vue' // 
import glyMagessitem4 from '@/pages/ywbl/glyMagess/item4.vue' // 
import glyMagessitem5 from '@/pages/ywbl/glyMagess/item5.vue' // 

import user from '@/pages/user'  //
import item1 from '@/pages/user/item1.vue' // 2
import item2 from '@/pages/user/item2.vue' // 

import pbgl from '@/pages/pbgl'  //
import pbglitem1 from '@/pages/pbgl/item1.vue' 
// import pbglitem2 from '@/pages/pbgl/item2.vue' 
// import pbglitem3 from '@/pages/pbgl/item3.vue' 
// import pbglitem4 from '@/pages/pbgl/item4.vue' 






Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect:"/ywbl"
    },
    {
      path: '/',
      redirect:"/login"
    },
    {
      path: '/ywbl',
      name: '',
      component: ywbl,
      redirect:"/input",
      children:[
        {path: '/infor',name: '',component: infor},
        {path: '/table',name: '',component: table},
        {path: '/tabltableItem',name: '',component: tabltableItem},
        {path: '/tableItemChild',name: '',component: tableItemChild},
        {path: '/input',name: '',component: input},
        {path: '/inputItem',name: '',component: inputItem},
        {path: '/inputItemChild',name: '',component: inputItemChild},
        {path: '/inputItemChild1',name: '',component: inputItemChild1},
        {path: '/glyMagessitem1',name: ' ',component: glyMagessitem1},
        {path: '/glyMagessitem2',name: ' ',component: glyMagessitem2},
        {path: '/glyMagessitem3',name: ' ',component: glyMagessitem3},
        {path: '/glyMagessitem4',name: ' ',component: glyMagessitem4},
        {path: '/glyMagessitem5',name: ' ',component: glyMagessitem5},
      ]
    },
    {
      path: '/user',
      name: '',
      component: user,
      redirect:"/item1",
      children:[
        {path: '/item1',name: '',component: item1},
        {path: '/item2',name: '',component: item2},
      ]
    },
    {
      path: '/pbgl',
      name: '',
      component: pbgl,
      redirect:"/pbglitem1",
      children:[
        {path: '/pbglitem1',name: '',component:pbglitem1},
        {path: '/pbglitem2',name: '',component:inputItem},
        {path: '/pbglitem3',name: '',component:inputItemChild},
        {path: '/pbglitem4',name: '',component:inputItemChild1},
      ]
    },
    {
      path: '/login',
      name: '',
      component: login,
    },
  ]
})



