<template>
  <div class="content">
      <div class="top">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item >
            <el-input v-model="formInline.user" placeholder=""></el-input>
          </el-form-item>
       
          <el-form-item>
            <el-button type="primary" ></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
      </div>

      <div class="table">
                <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
              label=""
             >
              <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.name }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              prop="office"
             >
            </el-table-column>
            <el-table-column
              label=""
              prop="exe"
             >
            </el-table-column>
            <el-table-column width="200" label="">
              <template slot-scope="scope">
                <!--  -->
                <el-button
                  size="mini"
                  type="primary"
                  @click="go(scope.row.meshods)"                  
                  >{{scope.row.meshods}}</el-button>
              </template>
            </el-table-column>
          </el-table>
      </div>
      <div class="page">
              <el-pagination
              background
              layout="prev, pager, next"
              :total="1000">
      </el-pagination>

      </div>
  </div>
</template>

<script>
  export default {
    
  }
</script>

<style lang="scss" scoped>
  .top{
    background-color: #fff;
    padding: 5px 0px 5px 10px;
  }.table{
    margin-top: 0px;
  }
  .page{
    margin-top: 10px;
    text-align: center;
  }
</style>


<script>
  export default {
    data() {
      return {
         tableData: [{
          date: '2016-05-02',
          name: '',
          type:"",
          office:"",
          exe:"1",
          meshods:""

        }, {
          date: '2016-05-04',
          name: '',
          type:"",
          office:"",
          exe:"2",
          meshods:""

        }, {
          date: '2016-05-01',
          name: '',
          type:"",
          office:"",
          exe:"3",
          meshods:""


        }, {
          date: '2016-05-03',
          name: '',
          type:"",
          office:"",
          exe:"4",
          meshods:""

        }],
        formInline: {
          user: '',
          region: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
       go(v){
      if(v==""){
        this.$router.push("/inputItemChild1")
      }
    }
    },
   
  }
</script>


