<template>
  <div class="content">
      <div class="top" v-if="lock">
        <h1></h1>
      </div>
      <div class="top" v-if="!lock">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="formInline.user" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.region" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" ></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary" @click="onAdd"></el-button>
        <el-button type="primary"></el-button>
        <el-button type="primary"></el-button>
      </div>

      <div class="table">
                <el-table
            :data="tableData"
            style="width: 100%">

              <el-table-column
              type="selection"
              width="55">
              </el-table-column>
            <el-table-column
              label=""
              sortable
             >
              <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.project }}</span>
              </template>
            </el-table-column>
           
               <el-table-column
              label=""
             >
              <template slot-scope="scope">
             
                <span style="margin-left: 10px">{{ scope.row.name }}</span>
              </template>
            </el-table-column>

                <el-table-column
              label=""
              width="400"
             >
              <template slot-scope="scope">
              
                <span style="margin-left: 10px">{{ scope.row.address }}</span>
              </template>
            </el-table-column>
        
            <el-table-column
              label=""
             >
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.name }}</el-tag>
                  </div>
              </template>
              
            </el-table-column>
            <el-table-column width="150" label="">
            <template>
                <el-button type="primary" v-text="lock ? '': ''"></el-button>
              </template>
            </el-table-column>
          </el-table>
      </div>
    
  </div>
</template>

<script>
  export default {
    
  }
</script>

<style lang="scss" scoped>
  .top{
    background-color: #fff;
    padding: 5px 0px 5px 10px;
  }.table{
    margin-top: 10px;
  }
</style>


<script>
  export default {
    data() {
      return {
        lock:false,//
         tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 ',
          project:""
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 ',
          project:""
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 ',
          project:""
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 ',
          project:""
        }],
        formInline: {
          user: '',
          region: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      onAdd(){
        this.lock =true
      }
    }
  }
</script>


